import { Component, ViewChild } from '@angular/core';
import { App, IonicPage, NavController, NavParams, ViewController, AlertController, LoadingController, ActionSheetController } from 'ionic-angular';
import { Api, GameProvider } from '../../providers/providers';
import { StorageProvider } from "../../providers/storage/storage";
import { Users } from '../../providers/users/users';
import { ImagePicker } from '@ionic-native/image-picker';
